@import "styles/variables";
@import "styles/mixins";

.fieldset {
  @include resetFieldset;
}

.label {
  @include regular($colorFontLightest, 500);
}

.label + .input {
  margin-top: 0.65rem;
}

.input {
  @include regular($colorFontLightest, 500);

  margin: 0;
  padding: 0 1rem;
  height: 5rem;
  width: 100%;
  background-color: $colorBgLightestTransparent1;
  border: 1px solid $colorBgLightest;
  transition-duration: $transitionDuration;

  &:hover,
  &:focus {
    border-color: $colorBgLightestTransparent3;
  }

  &:focus {
    background-color: transparent;
  }
}

.inputLarge {
  height: 6rem;
}

.inputError {
  border-color: orange;

  &:focus {
    border-color: orange;
  }
}
