@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: 45rem 1fr;
  grid-column-gap: 9rem;
  padding-left: 10rem;
}

.heading,
.description {
  grid-column: 1 / 2;
}

.description {
  margin-top: 4rem;
}

.list {
  @include resetList;

  grid-column: 2 / 3;
  grid-row: 1 / 4;
  display: flex;
  align-items: center;
}

.item {
  &:not(:first-child) {
    margin-left: -10rem;
  }
}

.image {
  width: 30rem;
}

.wrapperRTL {
  grid-template-columns: 1fr 45rem;
  padding-left: 0;

  .heading,
  .description {
    grid-column: 2 / 3;
  }

  .list {
    @include resetList;

    grid-column: 1 / 2;
    grid-row: 1 / 4;
  }
}
