@import "variables";

// font mixins
@mixin h1 ($color: $colorFontRegular) {
  font-family: "Raleway", Arial, sans-serif;
  font-size: 4.8rem;
  font-weight: 700;
  line-height: 6rem;
  color: $color;
}

@mixin h2 ($color: $colorFontRegular) {
  font-family: "Raleway", Arial, sans-serif;
  font-size: 4rem;
  font-weight: 700;
  line-height: 6rem;
  color: $color;
}

@mixin h3 ($color: $colorFontRegular, $weight: 700) {
  font-family: "Raleway", Arial, sans-serif;
  font-size: 2.4rem;
  font-weight: $weight;
  line-height: 2.9rem;
  color: $color;
}

@mixin highlight($color: $colorFontRegular) {
  font-family: "Raleway", Arial, sans-serif;
  font-size: 2rem;
  font-weight: 500;
  line-height: 3rem;
  color: $color;
}

@mixin regular($color: $colorFontRegular, $weight: 400) {
  font-family: "Raleway", Arial, sans-serif;
  font-size: 1.6rem;
  font-weight: $weight;
  line-height: 1.9rem;
  color: $color;
}
// reset mixins

@mixin resetButton {
  padding: 0;
  font-size: 0;
  background-color: transparent;
  border: none;
  box-shadow: none;
  border-radius: 0;
  cursor: pointer;
}

@mixin resetList {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin resetFieldset {
  margin: 0;
  padding: 0;
  border: none;
}

// style mixins
@mixin visuallyHidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  font-size: 0;
}
