@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.heading {
  @include visuallyHidden;
}

.stats {
  grid-column: 2 / 3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 0;
}

.list {
  display: flex;
  align-items: center;
  margin-top: 5rem;
}

.item {
  @include h2;

  position: relative;
  line-height: 4.7rem;
  text-indent: 8rem;

  &::before {
    content: "";
    position: absolute;
    top: 1.5rem;
    left: 4rem;
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
  }

  &:not(:first-child) {
    margin-left: 11rem;
  }
}

.itemInhabitants {
  &::before {
    background-color: $colorBgBullets;
  }
}

.itemBuildings {
  &::before {
    background-color: $colorBgDarkest;
  }
}

.slogan {
  margin-top: 7rem;
}
