@import "styles/variables";
@import "styles/mixins";

.footer {
  background-color: $colorBgLighter;
}

.wrapper {
  display: grid;
  grid-template-columns: 1fr 41.5rem 1fr;
  padding-top: 6rem;
  padding-bottom: 6rem;
}

.logo {
  width: 12rem;
  fill: $colorBgLightest;
}

.heading {
  margin-top: 7rem;
}

.list {
  @include resetList;

  margin-top: 1rem;
}

.item {
  &:not(:first-child) {
    margin-top: 0.2rem;
  }
}

.link {
  @include highlight($colorFontLightest);

  transition-duration: $transitionDuration;

  &:hover,
  &:focus {
    opacity: 0.7;
  }
}

.apps {
  display: flex;
  flex-direction: column;
  padding-left: 12.5rem;
}

.store {
  margin-top: 2rem;
  font-size: 0;

  &:hover,
  &:focus {
    .icon {
      fill: $colorBgBullets;
    }
  }
}

.icon {
  width: 17rem;
  fill: $colorFontLightest;
  transition-duration: $transitionDuration;
}
