@import "styles/variables";
@import "styles/mixins";

.section {
  --padding-left: 65rem;
  position: relative;
  background-color: $colorFontHighlightDark;
  background-repeat: no-repeat;
  background-size: 60rem auto;
}

.wrapper {
  padding-top: 9.6rem;
  padding-bottom: 13rem;
}

.heading {
  display: block;
  padding-left: var(--padding-left);
}

.list {
  @include resetList;

  display: flex;
  align-items: center;
  margin-top: 5.3rem;
  padding-left: var(--padding-left);
}

.item {
  font-size: 0;

  &:not(:first-child) {
    margin-left: 5rem;
  }
}

.iconApple,
.iconGoogle {
  height: 5.6rem;
  fill: $colorBgLightest;
  transition-duration: $transitionDuration;

  &:hover {
    fill: $colorBgBullets;
  }
}
