@import "styles/variables";
@import "styles/mixins";

.extWrapper {
  position: absolute;
  left: 0;
  width: 100%;
  background-color: $colorBgMain;
  transition-duration: $transitionDuration;
  transform: translateY(0);
  z-index: 1;
}

.wrapper {
  display: flex;
  align-items: center;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.logo {
  width: 16.5rem;
  fill: $colorFontHighlightDark;
}

.buttonLogo,
.buttonContacts {
  @include resetButton;
}

.buttonContacts {
  @include regular($colorFontDarkest);

  margin-left: auto;
}
