@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: flex;
  flex-direction: column;
  padding: 6.5rem 3rem 4.7rem 3rem;
  background-color: $colorBgLightest;
}

.heading {
  text-align: center;
  text-transform: uppercase;
}

.price {
  @include h3($colorFontHighlightLighter);

  margin-top: 2rem;
  text-align: center;
}

.description {
  margin-top: 6rem;
}
