@import "styles/mixins";

.wrapper {
  padding-top: 6.5rem;
}

.heading,
.description {
  padding-right: 50%;
  padding-left: 11rem;
}

.description {
  @include highlight;

  position: relative;
  margin: 2rem 0 0 0;
  z-index: 1;
}

.icon {
  margin-top: -15rem;
  margin-bottom: -15rem;
  width: 100%;
  height: 70rem;
  transform: scaleX(-1);
}
