@import "styles/variables";

.section {
  position: relative;
  padding-top: 9.3rem;
  padding-bottom: 11.5rem;
  min-height: 61.3rem;
  background-color: $colorBgLighter;
}

.icon {
  position: absolute;
  bottom: 0;
  fill: rgba(255, 255, 255, 0.08);
}

.heading {
  text-align: center;
}

.form {
  position: relative;
  display: grid;
  grid-template-columns: 100%;
  grid-row-gap: 2.3rem;
  width: 63rem;
  margin: 4.6rem auto 0 auto;
}

.doubleColumn {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-column-gap: 2rem;
}

.button {
  margin: 2.2rem auto 0 auto;
  width: 41.5rem;
}
